a.button-link {
    text-decoration: none;
    color: white;
  }
  
  /* 样式化链接为按钮 */
  a.button-link {
    display: inline-block;
    background-color: #007bff; /* 按钮背景色 */
    color: white; /* 按钮文字颜色 */
    padding: 10px 20px; /* 内边距 */
    border-radius: 5px; /* 圆角 */
    transition: background-color 0.3s ease; /* 背景色过渡效果 */
  }
  
  /* 鼠标悬停时的样式 */
  a.button-link:hover {
    background-color: #0056b3; /* 鼠标悬停时的背景色 */
  }