/*for APP's logo on website, default*/
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


/* App.css */
.map {
  width: 100%;
  height: 100vh; 
}

body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: Arial, sans-serif;
}

/* App.js */
.main {
  display: flex;
  height: calc(100vh - 50px); /* Adjust height based on your header/footer size */
}

.map-container {
  display: flex;
  position: relative;
  flex: 70%;
  height: 100%;
}

.all-toilet-list {
  flex: 30%;
  padding: 20px;
  overflow-y: auto;
}

.share-your-toilet-button {
  position: absolute;
  bottom: 10px;
  left: 10px;
  z-index: 1000;
  padding: 15px 20px;
  background-color: #007BFF;
  color: white;
  border: none;
  border-radius: 10px;
  cursor: pointer;
}

.to-curent-location-button {
  position: absolute;
  bottom: 65px;
  left: 10px;
  z-index: 1000;
  padding: 15px 20px;
  background-color: #007BFF;
  color: white;
  border: none;
  border-radius: 10px;
  cursor: pointer;
}



.shared-toilet-button:hover {
  background-color: #85befc;
}

.to-curent-location-button :hover {
  background-color: #85befc;
}


header {
  display: flex;
  justify-content: flex-end;
  padding: 10px;
  background-color: #282c34;
}

.auth-button {
  margin: 0 10px;
  padding: 10px 20px;
  color: white;
  background-color: #007BFF;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.auth-button:hover {
  background-color: #0056b3;
}