/* AllToilet.css */

.all-toilet-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start; /* 确保所有内容靠上对齐 */
    padding: 5px;
  }
  
  .toilet-list {
    list-style-type: none;
    padding: 0;
    margin: 0;
  }
  
  .toilet-item {
    margin-bottom: 15px;
  }
  
  .toilet-item strong {
    display: block;
    margin-bottom: 5px;
  }
  